@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap');

html {
  height: 100%;
  overflow-x: hidden;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
  -webkit-text-size-adjust: none;
  -ms-content-zooming: none;
  background: #6547FF;
}

* {
  font-family: "Space Grotesk", sans-serif;
  color: white;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-weight: 700;
  font-size: 3rem;
  letter-spacing: -0.04em;
  line-height: 1.2em;
}

h2 {
  font-weight: 600;
  font-size: 2rem;
  letter-spacing: -0.02em;
  line-height: 1.2em;
}

body {
  background: #6547FF;
  position: relative;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  overflow-x: hidden;
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
}
.bottom {
  position: absolute;
  bottom: 0;
}

.top-bar {
  border-bottom: solid 0.5px #9F8CFF;
}

.bottom-bar {
  border-top: solid 0.5px #9F8CFF;
}

.metamask {
  color: #E68A3C;
}
.bar {  
  width: 100%;
  height: 3rem;
}

.bar p {
  font-size: 1rem;
  display: inline-block;
  line-height: 3rem;
  color: #C8C2F2;
  letter-spacing: 0.02rem;
}

.bar p:first-child {
  float: left;
  padding-left: 1.5rem;
}

.bar p:last-child {
  float: right;
  padding-right: 1.5rem;
}
.content {
  width: 100%;
  // height: 100%;
  
  // background: linear-gradient(135deg, #6666ff, #d966ff);
  

}
@keyframes rotate {
  from {transform: rotate(0deg);}
  to {transform: rotate(360deg);}
}

.circular-text {
  animation: rotate 9s linear 0s infinite forwards; 
  transform-origin: center;
}

.center-text {
  text-align: center;
}

.twitter {
  color: #1D9BF0;
}

.story {
  text-align: center;
  font-size: 1.375rem;
  line-height: 2.125rem;
  font-weight: 500;
  // letter-spacing: ;
  padding-top: 4rem;
  max-width: calc(900px + 3rem);
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  box-sizing: border-box;
  margin: 0 auto;
}

.socials {
  // float: right;
  // width: 100%;
  position: absolute;
  display: inline-block;
  padding-top: 0.625rem;
  // position: absolute;
  right: 1.5rem;
  // left: 50%;
  // transform: translate(-50%);

  a {
    display: inline-block;

    img {
      padding: 0 1rem;
      width: 1.75rem;
      height: 1.75rem;
    }
  }
}
.tiktok {
  color: #EE1D52;
}
.meta {
  
  background: -webkit-linear-gradient(0deg, #33FF77, #4DE1FF);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.instagram {
  background: -webkit-linear-gradient(45deg, #FEC01D, #F1010E, #C000A1);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.footer {
  padding: 3rem 0;
}
.roadmap-container {

  position: relative;
  margin-left: auto;
  margin-right: auto;
  max-width: 800px;
  padding: 4rem 1.5rem;
  box-sizing: border-box;
  
h1 {
  position: relative;
  display: block;
  text-align: center;
  padding-bottom: 1rem;
}
 
}

.done-capsule {
  position: absolute;
    display: inline-block;
    border-radius: 1.5rem;
    background: linear-gradient(135deg, #C61AFF, #FF4DA6);
    height: 3rem;
    line-height: 3rem;
    padding: 0 1rem;
    font-weight: bold;
    font-size: 1.5rem;
    width: 6rem;
    text-align: center;
    left: 0;
    // top: 2rem;
}

.capsule {
  position: absolute;
    display: inline-block;
    border-radius: 1.5rem;
    background: #414141;
    height: 3rem;
    line-height: 3rem;
    padding: 0 1rem;
    font-weight: bold;
    font-size: 1.5rem;
    width: 6rem;
    text-align: center;
    left: 0;
    // top: 2rem;
}

.airbnb {
  color: #FF5A5F;
}

a {
  text-decoration: none;
}
.spotify {
  color: #65D46E;
}

.sequoia {
  color: #317156;
}

.discord {
  background-color: #5865F2;
  padding: 0.0625rem 0.625rem;
  border-radius: 1rem;
  // background: -webkit-linear-gradient(0deg, #C61AFF, #FF4DA6);
  // -webkit-background-clip: text;
  // -webkit-text-fill-color: transparent;
}
.party {
  background: -webkit-linear-gradient(0deg, #FF3445, #FF9F1A);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.quarter {
  position: relative;
  width: 100%;
  margin-top: 2rem;

  

  &::after {
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    height: 0.5px;
    background: #4D4D4D;
  }

  &:last-child::after {
    content: none;
  }
}




.milestone {
  display: inline-block;
  box-sizing: border-box;
  padding-left: 10rem;

  h2 {
    line-height: 3rem;
    
  }
  p {
    color: #CCCCCC;
    padding: 1rem 0 2rem 0;
  }

  span {
    font-weight: 600;
  }
}

.container {
  position: relative;
  width: 100%;
  height: 100%;
}
.discord-link {
  position: fixed;
  display: inline-block;
  bottom: 6rem;
  right: 3rem;
  cursor: pointer;
  z-index: 1;

}
.discord-link span {
  display: inline-block;
  
}
.join-discord {
  position: relative;
  z-index: -1;
  transform: scale(1.5);
  
}
.hero {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  background: #6547FF;
  z-index: 1;
}
.roadmap {
  position: absolute;
  top: 100%;
  width: 100%;
  // height: 100%;
  background: #272727;
}

.titleContent {
  padding: 2.5rem 0;
  text-align: center;
}

.logo {
  position: absolute;
  width: auto;
  height: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.mint-section {

  position: absolute;
  bottom: 5rem;
  width: 100%;
  text-align: center;

}


.mint-button {
  display: inline-block;
  position: relative;
  font-weight: 500;
  background: #272727;
  height: 4rem;
  border: none;
  border-radius: 2rem;
  font-size: 1.25rem;
  padding: 0 1.5rem;
  cursor: pointer;
}

.mint-label {
  position: relative;
  display: inline-block;
  font-size: 1.125rem;
  color: #ffffff;
  padding: 0 1.5rem;
  // height: 2rem;
  border-radius: 1rem;
  // vertical-align: middle;
  // white-space: nowrap;
  line-height: 1.5rem;
  margin-bottom: .125rem;
  opacity: 0;
  transition: all cubic-bezier(0.0, 0.0, 0.3, 1.0) 0.15s;
  font-weight: 500;
  // -webkit-transition: all ease-out;
}

.mint-label-animation {
  opacity: 1.0;
  margin-bottom: 0.5rem;
  transition: all cubic-bezier(0.0, 0.0, 0.3, 1.0) 0.15s;
  // -webkit-transition: all ease-out;
}

.image-section {
  position: relative;
  // display: block;
  box-sizing: border-box;
  padding: 0 4rem;
  width: 100%;
  height: 16rem;
  // text-align:center;

}

.image-section > img {
  // left: 0;
  // vertical-align: top;
  position: absolute;
  // left: 50%;
  // transform: translate(-50%);
  // width: calc(100%/ 5);  
  width: auto;
  height: 16rem;
  display: inline-block;
}
.logotype {
  height: 5rem;
  width: auto;
}
.hero-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  background-position: center center;
  background-image: url(../assets/hero.png);
  background-size: cover;
  padding-top: calc((200/720)*100%);
  z-index: -1;
}

.image-section > img:nth-child(4) {
  left: calc((50% + 12rem)/2);
  transform: translate(-8rem, -4rem);
}

.image-section > img:nth-child(3) {
  left: 50%;
  transform: translate(-50%);
}

.image-section > img:nth-child(2) {
  left: calc(((50% - 12rem)/2) + 50%);
  transform: translate(-50%);
}

.image-section > img:nth-child(1) {
  right: 4rem;
}





@media screen and (max-width: 480px) {
  h1 {
    font-size: 2.25rem;
  }

  .logotype {
    height: 3.5rem;
  }
  h2 {
    font-size: 1.5rem;
  }
  .mint-button {
    font-weight: 500;
    background: #272727;
    height: 3rem;
  }
  .logo {
    width: 33%;
  }
  .bar p {
    font-size: .875rem;
  }
  .bar p:first-child {
    float: left;
    padding-left: 1rem;
  }
  
  .bar p:last-child {
    float: right;
    padding-right: 1rem;
  }
  .roadmap-container {
    padding: 4rem 1rem;
  }
  .socials {
   
    right: 1rem;
    a {
     
  
      img {
        padding: 0 0.5rem;
      }
    }
  }


  .join-discord {
    
    transform: scale(1);
    
  }

  .capsule {
    position: relative;
    margin-bottom: 1rem;
    width: 4.5rem;
    height: 2rem;
    line-height: 2rem;
    font-size: 1.125rem;
  }
  .done-capsule {
    position: relative;
    margin-bottom: 1rem;
    width: 4.5rem;
    height: 2rem;
    line-height: 2rem;
    font-size: 1.125rem;
  }

  .milestone {
    padding-left: 0rem;
    h2 {
      line-height: 2rem;
    }

    p {
      line-height: 1.5rem;
    }
  }
  .discord-link {
    bottom: 6rem;
  right: 1rem;
  }
  .mint-section {
    
    bottom: 6rem;
  }
}